import React, { useEffect, useState } from "react";
import "../../lib/components/fileupload/filupload.css";
import AppBar from "@material-ui/core/AppBar";
import Toolbar from "@material-ui/core/Toolbar";
import { makeStyles } from "@material-ui/core/styles";
import UploadService from "../../service/FileUploadService";
import ProgressBar from "react-bootstrap/ProgressBar";
import "bootstrap/dist/css/bootstrap.min.css";
import { Grid } from "@material-ui/core";
import Toast from "../../lib/components/toast/ErrorToast";
import { useHistory } from "react-router-dom";


const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 3,
  },
  menuButton: {
    marginRight: theme.spacing(100),
  },
  title: {
    flexGrow: 1,
  },
  input: {
    display: "none",
  },
  center: {
    display: "none",
  },

}));

const UploadDocument = (props) => {
  console.log("props in upload document", props);
  const classes = useStyles();
  const [selectedFiles, setSelectedFiles] = useState(undefined);
  const [currentFile, setCurrentFile] = useState(undefined);
  const [progress, setProgress] = useState(0);
  const [message, setMessage] = useState("");
  const [success,setSuccess]=useState(0);
  const allowedFileTypes=['pdf','doc','png','docx','jpg','jpeg'];
  const uploadType=props.popUpParams.uploadType;
  const [showFileSizeError,setShowFileSizeError]=useState(false);
  const [showFileFormatError,setShowFileFormatError]=useState(false);
  let history = useHistory();

  const selectFile = (event) => {
    setSelectedFiles(event.target.files);

  }

  const land = () => {
    props.land(1);
  }

  useEffect(()=>{
    if(selectedFiles)
    {
      setShowFileFormatError(false);
      setShowFileSizeError(false);
      let currentFile = selectedFiles[0];
      let type=currentFile.name.split('.')[1];
 

        if(allowedFileTypes.some(item => type === item))
        { 
         if(currentFile.size<=500000)
         {

          setProgress(20);
          setCurrentFile(currentFile);

     
      UploadService.upload(currentFile,props.popUpParams ,(event) => {
      
        setProgress(Math.round((100 * event.loaded) / event.total));
      })
        .then((response) => {
            if (response.isAxiosError) 
            {
               console.log("error");
               setProgress(0);
               setMessage("Could not upload the file!");
               setCurrentFile(undefined);
            }
            else{
             console.log("response success");
             setSuccess(1);
             props.onSuccess(response.data.dataItems[0]);
           }
         
         
        })
        .catch(() => {
          console.log("error");
          setProgress(0);
          setMessage("Could not upload the file!");
          setCurrentFile(undefined);
        });
  
      setSelectedFiles(undefined);
       }
      
      else
      {
        setShowFileSizeError(true);
      }
     }
        else
        {
          setShowFileFormatError(true);
       }

    }

  },[selectedFiles]);

  const RenderToolBar=()=>
  {
    if(progress===100)
    {
      return(
            
    <Toolbar>
    <label for="image">
      <input
        type="file"
        name="image"
        id="image"
        className={classes.center}
        
      />
      {uploadType==='invoice' &&
      <img
        src="./uploaded_invoice.png"
        style={{
          position: "relative",
          marginLeft: "120%",
          width: "80%",
        }}
              />}
             {uploadType==='POD' &&
      <img
        src="./uploaded_pod.png"
        style={{
          position: "relative",
          marginLeft: "120%",
          width: "80%",
        }}
      />}
      <div></div>
    </label>
  </Toolbar>
      
      )
    }
    else{
      return(

       
        
        <Toolbar>
        <label for="image">
          <input
            type="file"
            name="image"
            id="image"
            className={classes.center}
            onChange={selectFile}
          />
             {uploadType==='invoice' &&
          <img
            src="./upload_invoice.png"
            style={{
              position: "relative",
              marginLeft: "120%",
              width: "80%",
            }}
              />}
              {uploadType==='POD' &&
          <img
            src="./upload_pod.png"
            style={{
              position: "relative",
              marginLeft: "120%",
              width: "80%",
            }}
          />}
        </label>
      </Toolbar>
          
          )
    }
  }
    
   
   


  return (
    <>

    
      <div className={classes.root}>
        <AppBar
          position="static"
          style={{
            position: "relative",
            marginTop: "100px",
            height: "100px",
            centerTitle: true,
            backgroundColor:"#0E1B3D"
          }}
        >
          <RenderToolBar/>
        
        </AppBar>
      </div>
      <br></br>
      <br></br>
      <br></br>
      <br></br>

      {currentFile && (
        <>
        <div className="row">
          <div className="col-md-4"></div>
          <div className="col-md-5">
            <Grid container >
              <Grid item xs={12}>
              <ProgressBar variant="danger" now={progress} />
              </Grid>
              <Grid item xs={11}>
               {currentFile.name}
              </Grid>
              <Grid item xs={1}>
               {progress}%
              </Grid>


            </Grid>
           
          </div>
         
         </div>
         {/* <div className="row">
            <div className="col-md-4"></div>
          <div className="col-md-3">fileName</div>
          <div className="col-md-3">{progress}</div>
         </div> */}
         </>
      )}
      {message && (
         <div className="row">
          <div className="col-md-5"></div>
          <div className="col-md-3">
            <p style={{ color: "#FF0000" }}>{message}</p>
          </div>
        
        </div>

      )}
      <div className="row">
          <div className="col-md-5"></div>
          <div className="col-md-5">
      { showFileSizeError &&   <p style={{ color: "#C62926" }}>Allowed maximum size is 500 KB </p>}

      { showFileFormatError &&   <p style={{ color: "#C62926" }}>File format is not supported </p>}

      </div>
      </div>

      {progress === 0 && !message &&(
        <div className="row">
          <div className="col-md-3"></div>
          <div className="col-md-5">

            <p style={{ color: "#0E1B3D" }}>Only files with the following extensions are allowed *.pdf,.doc,.png,.jpg,.jpeg  </p>

          </div>
          <div className="col-md-3">
            <p>0%</p>
          </div>
        </div>
      )}
{props.popUpParams.status && progress===0 &&
<div className="row">
        <div className="col-md-2"></div>
        <div className="col-md-8">
          <p
            style={{
              textAlign: "center",
              position: "relative",
              color: "#0E1B3D",
            }}
          >
            Uploaded {uploadType} has been rejected by admin, please upload a new updated copy for verification.
      
          </p>
          <p  style={{
              textAlign: "center",
              position: "relative",
              color: "red",
            }}>Reason: {props.popUpParams.status.remarks}</p>
        </div>
          </div> }

      {(! props.popUpParams.status || (props.popUpParams.status && progress===100)) && <div className="row">
        <div className="col-md-2"></div>
        <div className="col-md-8">
          <p
            style={{
              textAlign: "center",
              position: "relative",
              color: "#0E1B3D",
            }}
          >
           {props.popUpParams.customMessage ? props.popUpParams.customMessage: 
            `Please note: Document will be sent to DT admin after uploading and
              will notify you if gets successfully verified`
          }
          </p>
        </div>
      </div>}


      { props.popUpParams.message!==undefined  && progress===100 && <div className="row">

        <div className="col-md-2"></div>
        <div className="col-md-8">
          <p
            style={{
              textAlign: "center",
              position: "relative",
              color: "grey",
            }}
          >
            {props.popUpParams.message[0]}
          </p>
          <p
            style={{
              textAlign: "center",
              position: "relative",
              fontColor: "#0E1B3D",
              fontWeight:"bold"
            }}
          >
            {props.popUpParams.message[1]}{props.popUpParams.message[2]}
          </p>
          <br></br>
          <p
            style={{
              textAlign: "center",
              position: "relative",
              color: "#0E1B3D",
            }}
          >
            {props.popUpParams.message[3]} </p>
          <p>
{props.popUpParams.message[4]}
          </p>
        </div>
      </div>} 
    </>
  );
};
//   />
// </>

export default UploadDocument;
